.container {
    width: 100%;
    height: auto;
    border: solid 1px rgba(160,160,160,1);

    > thead {
        > tr {
            >th {
                border-bottom: solid 1px rgba(160,160,160,1);
                border-right: solid 1px rgba(160,160,160,1);
                color: black;
                padding: 10px;
            }
        }
    }

    > tbody {
        > tr {
            > td {
                padding: 10px;
                border: solid 1px gray;
            }
        }
    }
}
