.container {
  width:100%;
  height:auto;
  display:flex;
  flex-direction:row;
  align-content:flex-start;

  > .leftpanel {
    display: flex;
    flex-direction:column;
    flex-grow: 0;

    height: 100%;
    width: 300px;
    
    
    .bar {
      position: fixed;
      top: 0px;
      left: 0px;
      bottom: 0px;
      width: 300px;
      background-color: rgba(10,186,181,1);
      z-index: 50;

      > .titlebar {
        margin-top: 10px;
        display: flex;
        width: 100%;
        height: 64px;
  
        > .sitelogo {
          float:left;
          width: 64px;
          height: 64px;
          margin-left: 5px;
  
          > img {
            width: 48px;
            height: 48px;
            margin: 8px;
          }
        }
  
        > .title {
          margin-left: 5px;
          line-height: 64px;
          color: white;
          font-size: 24px;
          font-weight: bold;
        }
      }
  
      > .sidemenu {
        display: flex;
        width: calc(100% - 20px);
        height: auto;
        flex-direction:column;
        margin-left: 10px;
  
        > .menuhoffset {
          width: 100%;
          height: 20px;
        }
  
        > .menu {
          width: 100%;
          padding: 10px 15px 5px 15px;
          color: white;
          font-weight: bold;
          font-size: 20px;
        }
       
        > .submenu {
          width: calc(100% - 30px);
          padding: 15px 15px;
          font-size: 14px;
          font-weight: bold;
          display: flex;
          flex-direction:row;
          color:white;
  
          > .title {
            width: calc(100% - 30px);
            height: auto;
            margin-left: 10px;
          }
  
          svg {
            width:20px;
            height:20px;
            fill: white;
          }
        }
  
        > .linkmenu {
          width: 100%;
          line-height: 48px;
          color: white;
          display: flex;
          flex-direction:row;
          cursor: pointer;
          background-color: transparent;
          border-width: 0px;
  
          &:hover{
            background-color: rgba(10,133,129);
          }
  
          > div {
            display: flex;
            flex-direction:row;
            width: calc(100% - 50px);
            padding: 0 25px;
  
            > .title {
              width: calc(100% - 30px);
              height: auto;
              margin-left: 10px;
              font-size: 14px;
              font-weight: normal;
              line-height: 48px;
              text-align: left;
            }
    
            svg {
              width:18px;
              height:18px;
              margin-top:15px;
              fill: white;
            }
          }
        }
      }
    }
  }

  > .chartpanel {
    width: calc(100% - 300px);
    height: 500px;
    display: flex;
    flex-direction: column;
    
    > .rowarea_title {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction:column;
      border-bottom: rgba(223,223,223,1) solid 1px;
      padding: 20px 0;
      background-color: white;

      > .prodtitle {
        display: flex;
        flex-direction:row;
        width: calc(100% - 50px);
        padding: 0 25px 15px 25px;
  
        > .title {
          width: calc(100% - 58px);
          height: auto;
          margin-left: 10px;
          font-size: 32px;
          font-weight: bold;
          line-height: 56px;
          color: rgba(10,186,181,1);
        }
  
        svg {
          width:40px;
          height:40px;
          margin-top:8px;
          fill: rgba(10,186,181,1);
        }
      }

      > .charttitle {
        width: calc(100% - 87px);
        padding: 0 10px 0 77px;
        margin-top: -35px;
        height: 48px;
        line-height: 48px;
        color: rgba(90,90,90,1);
        font-size: 22px;
        font-weight: bold;
      }
    }

    
    > .rowarea_content {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction:column;
      margin: 10px 0 0 0;

      > .chartarea {
        width: calc(100% - 32px);
        height: auto;
        margin: 10px 15px;
        background-color: white;
        border: rgba(223,223,226,1) solid 1px;
        display: flex;
        flex-direction:column;
        background-color: white;
  
        > .titlebar {
          width: 100%;
          height: auto;
          display: flex;
          flex-direction:row;
          border-bottom: rgba(223,223,226,1) solid 1px;
  
          > .title {
            width: auto;
            margin: 0 20px;
            padding: 20px 0;
            height: auto;
            font-weight: bold;
            font-size:18px;
            flex-grow: 1;
            color: rgba(10,186,181,1);
          }

          > .rightoptions {
            width: auto;
            margin: 0 40px;
            padding: 20px 0;
            height: auto;
            font-weight: bold;
            font-size:14px;
            flex-grow: 1;
            text-align: right;
          }
        }
  
        > .content {
          width: calc(100% - 40px);
          margin: 20px;
          height: 430px;
        }
      }

      > .emojiarea {
        width: calc(100% - 32px);
        height: auto;
        margin: 10px 15px;
        background-color: white;
        border: rgba(223,223,226,1) solid 1px;
        background-color: white;

        > .listarea {
          width: 100%;
          height: auto;
          display: flex;
          flex-wrap:wrap;
          flex-direction:row;
        }
      }
    }
  }
}
