.container {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(223, 223, 223, 1);

  > .titlebar {
    width: 100%;
    height: 70px;
    border-bottom: 1px solid rgba(223, 223, 223, 1);
    background-color: rgba(240, 240, 240, 1);

    > .content {
      padding: 15px 15px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;

      > .field {
        display: flex;
        flex-direction: column;

        > .title {
          color: rgba(10, 186, 181, 1);
          font-weight: bold;
        }

        > .value {
          color: rgba(100, 100, 100, 1);
        }
      }
    }
  }

  > .contentbar {
    width: 100%;
    height: auto;
    justify-content: space-between;

    > .content {
      padding: 15px 15px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      > span.sentiment {
        display: flex;
        flex-direction: row;
        margin: 0 40px 0 20px;
        padding-right: 20px;
        border-right: 1px solid rgba(200, 200, 200, 1);

        > .icon {
          width: 40px;
          height: 40px;
        }

        > .text {
          text-align: center;
          height: 40px;
          color: rgba(10, 186, 181, 1);
          font-weight: bold;
          margin-left: 10px;
        }
      }

      > .field {
        display: flex;
        flex-direction: column;

        > .title {
          color: rgba(60, 60, 60, 1);
          font-weight: bold;
        }

        > .value {
          color: rgba(120, 120, 120, 1);
        }
      }
    }
  }
}
