.bar {
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  width: 300px;
  background-color: rgba(10,186,181,1);
  z-index: 50;

  > .titlebar {
    margin-top: 10px;
    display: flex;
    width: 100%;
    height: 64px;

    > .sitelogo {
      float:left;
      width: 64px;
      height: 64px;
      margin-left: 5px;

      > img {
        width: 48px;
        height: 48px;
        margin: 8px;
      }
    }

    > .title {
      margin-left: 5px;
      line-height: 64px;
      color: white;
      font-size: 24px;
      font-weight: bold;
    }
  }

  > .sidemenu {
    display: flex;
    width: calc(100% - 20px);
    height: auto;
    flex-direction:column;
    margin-left: 10px;

    > .menuhoffset {
      width: 100%;
      height: 20px;
    }

    > .menu {
      width: 100%;
      padding: 10px 15px 5px 15px;
      color: white;
      font-weight: bold;
      font-size: 20px;
    }
   
    > .submenu {
      width: calc(100% - 30px);
      padding: 15px 15px;
      font-size: 14px;
      font-weight: bold;
      display: flex;
      flex-direction:row;
      color:white;

      > .title {
        width: calc(100% - 30px);
        height: auto;
        margin-left: 10px;
      }

      svg {
        width:20px;
        height:20px;
        fill: white;
      }
    }

    > .linkmenu {
      width: 100%;
      line-height: 48px;
      color: white;
      display: flex;
      flex-direction:row;
      cursor: pointer;
      background-color: transparent;
      border-width: 0px;

      &:hover{
        background-color: rgba(10,133,129);
      }

      > div {
        display: flex;
        flex-direction:row;
        width: calc(100% - 50px);
        padding: 0 25px;

        > .title {
          width: calc(100% - 30px);
          height: auto;
          margin-left: 10px;
          font-size: 14px;
          font-weight: normal;
          line-height: 48px;
          text-align: left;
        }

        svg {
          width:18px;
          height:18px;
          margin-top:15px;
          fill: white;
        }
      }
    }
  }
}