.container {
  width:100%;
  height:auto;
  display:flex;
  flex-direction:row;
  align-content:flex-start;

  > .leftpanel {
    display: flex;
    flex-direction:column;
    flex-grow: 0;

    height: 100%;
    width: 300px;
  }

  > .chartpanel {
    width: calc(100% - 300px);
    height: 500px;
    display: flex;
    flex-direction: column;
    
    > .rowarea_title {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction:column;
      border-bottom: rgba(223,223,223,1) solid 1px;
      padding: 20px 0;
      background-color: white;

      > .prodtitle {
        display: flex;
        flex-direction:row;
        width: calc(100% - 50px);
        padding: 0 25px 15px 25px;
  
        > .title {
          width: calc(100% - 58px);
          height: auto;
          margin-left: 10px;
          font-size: 32px;
          font-weight: bold;
          line-height: 56px;
          color: rgba(10,186,181,1);
        }
  
        svg {
          width:40px;
          height:40px;
          margin-top:8px;
          fill: rgba(10,186,181,1);
        }
      }

      > .charttitle {
        width: calc(100% - 87px);
        padding: 0 10px 0 77px;
        margin-top: -35px;
        height: 48px;
        line-height: 48px;
        color: rgba(90,90,90,1);
        font-size: 22px;
        font-weight: bold;
      }
    }

    
    > .rowarea_content {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction:column;
      margin: 10px 0 0 0;

      > .chartarea {
        width: calc(100% - 32px);
        height: auto;
        margin: 10px 15px;
        background-color: white;
        border: rgba(223,223,226,1) solid 1px;
        display: flex;
        flex-direction:column;
        background-color: white;
  
        > .titlebar {
          width: 100%;
          height: auto;
          display: flex;
          flex-direction:row;
          border-bottom: rgba(223,223,226,1) solid 1px;
  
          > .title {
            width: auto;
            margin: 0 40px;
            padding: 20px 0;
            height: auto;
            font-weight: bold;
            font-size:18px;
            flex-grow: 1;
            color: rgba(10,186,181,1);
          }

          > .rightoptions {
            width: auto;
            margin: 0 40px;
            padding: 20px 0;
            height: auto;
            font-weight: bold;
            font-size:14px;
            flex-grow: 1;
            text-align: right;
          }
        }
  
        > .content {
          width: calc(100% - 40px);
          margin: 20px;
          height: 430px;
          display: flex;
          flex-direction: column;
          
          > .toolbar {
            display: flex;
            margin: 5px 0;
        
            input {
              width: 50%;
            }
        
            button {
              margin-left: 3px;
              border-width: 0px;
              cursor: pointer;
              fill:rgba(120,120,120,1);

              &:hover {
                background-color: rgba(245,245,245,1);
                fill:rgba(60,60,60,1);
              }
            }
        
            button.export {
              margin-left: auto;
            }
          }
        
          > .comments {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            overflow: auto;
            flex-grow: 1;
            flex-shrink: 1;
            border-radius: 0px;
            background-color: rgba(248,248,248,1);
        
            > * {
              margin-bottom: 20px;
            }
        
            > .info {
              align-self: flex-start;
            }

            > .loadinginfo {
              color : rgba(10,186,181,1);
              font-weight: bold;
            }

            > .noavailinfo {
              font-weight: bold;
            }

            > .errorinfo {
              color : red;
              font-weight: bold;
            }
          }
        
          > .pagination {
            align-self: center;
          }
        }
      }
    }
  }
}
