.container {
  width : 320px;
  height: 160px;
  margin: 20px 20px;
  border: rgba(223,223,226,1) solid 1px;
  border-radius: 10px;
  display: flex;
  flex-direction:row;

  > .col1 {
    width: 119px;
    height: calc(100% - 15px);
    margin: 10px 5px 10px 5px;
    display: flex;
    flex-direction:column;
    border-right: rgba(223,223,226,1) solid 1px;

    > .icon {
      width: 60px;
      height: 60px;
      margin: 20px 30px;
    }

    > .desc {
      width: 100%;
      height: auto;
      font-size: 12px;
      font-weight: bold;
      text-align: center;
    }
  }

  > .col2 {
    position: relative;
    width: 180px;
    height: calc(100% - 15px);
    margin: 10px 5px 10px 5px;

    > .count {
      width: 100%;
      height: auto;
      line-height: 60px;
      margin: 20px 0;
      font-weight: bold;
      text-align: center;
      font-size: 48px;
    }

    > .action {
      width: 100%;
      height: auto;
      font-weight: bold;
      text-align: center;
      font-size: 12px;
      color: rgba(10,186,181,1);
      cursor: pointer;

      &:hover{
        color: rgba(10,133,129);
      }
    }
  }
}